<template>
  <div id="homeList">
    <div class="title">设备栏
      <b-btn style="float:right" v-b-modal="'modal1'">绑定设备</b-btn>
    </div>

    <b-modal ref="modal1" id="modal1" title="绑定设备" @ok="handleOk" @shown="clearData">      
      <b-input-group size="lg" prepend="序列号" class="mb-4">
        <b-form-input
          :value="serialNumber"
          @input="updateSerialNumber"
          ></b-form-input>
      </b-input-group>
      <b-input-group size="lg" prepend="密码" class="mb-4">
        <b-form-input
          :value="password"
          @input="updatePassword"
          ></b-form-input>
      </b-input-group>
    </b-modal>

        <!-- :img-src="require(`${baseUrl}C1.png`)" -->
    <div class="wrapper">
      <!-- <div class="item" v-if="deviceList" v-for="(item, index) in deviceList" :key="index">{{item.serialNumber}}</div> -->
      <b-card
        v-for="(item, index) in deviceList"
        :key="index"
        :title="item.serialNumber ? item.serialNumber : item.alias"
        :img-src="require(`./../../../../public/device_icon/C1.png`)"
        img-alt="Image"
        img-top
        style="max-width: 20rem"
        tag="div"
        class="mb-2 mr-4 item"
        >
        <p class="card-text">{{item.status}}</p>
      </b-card>
    </div>

  </div>
</template>

<script>
require
import { mapActions, mapState, mapMutations } from 'vuex'
export default {
  name: 'DeviceList',
  data() {
    return {
      baseUrl: `./../../../../public/device_icon/`
    }
  },
  mounted () {
    this.getDevices()
  },
  computed: {
    ...mapState(('home'), [
      'deviceList',
      'serialNumber',
      'password'
    ])
  },
  methods: {
    ...mapActions(('home'), [
      'getDevices',
      'bindDevice'
    ]),
    ...mapMutations(('home'), [
      'updateSerialNumber',
      'updatePassword',
      'clearData'
    ]),
    handleOk (evt) {
      evt.preventDefault()
      if (!this.serialNumber) {
        alert('请输入序列号')
      } else if (!this.password) {
        alert('请输入密码')
      } else {
        this.bindDevice()
        this.$refs.modal1.hide()
      }
    }
  }
}
</script>

