import http from '../../http'
import router from '../../router'

const state = {
  items: null,
  serialNumber: null,
  password: null,
  type: null
}

// getters
const getters = {}

// actions
const actions = {
  getBackupDevices ({state, commit}) {
    return http().get('/backup')
      .then( ({data}) => {
        commit('setBackupDevices', data)
      })
      .catch(({err}) => {
        console.log(err)
      })
  },
  addDevice ({state, commit, dispatch}) {
    return http().post('/backup', {
      serialNumber: state.serialNumber,
      password: state.password,
      type: state.type
    })
      .then( (res) => {
        // console.log(res)
        commit('clearData')
        dispatch('getBackupDevices')
      })
      .catch( ({response}) => {
        // console.log(response)
        alert('序列号或密码不能重复，请检查')
      })
  }
}

const mutations = {
  setBackupDevices (state, data) {
    state.items = data
  },
  updateSerialNumber (state, value) {
    state.serialNumber = value
  },
  updatePassword (state, value) {
    state.password = value
  },
  updateType (state, value) {
    state.type = value
  },
  clearData (state) {
    state.serialNumber = null
    state.password = null
    state.type = null
  }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}