import http from '../../http'
import router from '../../router'

const state = {
  deviceList: null,
  serialNumber: null,
  password: null
}

// getters
const getters = {}

// actions
const actions = {
  getDevices ({state, commit}) {
    return http().get('/devices')
      .then(( {data} ) => {
        // console.log(data)
        commit('setDeviceList', data)
      })
      .catch(err => {
        console.log(err)
      })
  },
  bindDevice ({state, commit, dispatch}) {
    return http().post('/devices', {
      serialNumber: state.serialNumber,
      password: state.password
    })
      .then( (res) => {
        // console.log(res)
        if (res.data.msg) {
          alert(res.data.msg)
          return
        }
        dispatch('getDevices')
      })
      .catch( (err) => {
        console.log(err)
      })
  }
}

const mutations = {
  setDeviceList(state, data) {
    state.deviceList = data
  },
  updateSerialNumber (state, value) {
    state.serialNumber = value
  },
  updatePassword (state, value) {
    state.password = value
  },
  clearData (state) {
    state.serialNumber = null
    state.password = null
  }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}