<template>
  <div id="admin">
    <header>卓绝智能后台操作界面</header>
    <b-container>
      <div class="title">设备列表</div>
      <b-table
        striped
        hover
        :items="items"
        :fields="fields"
        bordered
        >
      </b-table>
    </b-container>
    <b-container class="bv-example-row">
      <b-row align-h="center">
        <b-col cols="2">
          <b-button v-b-modal.modal1 size="lg">添加设备</b-button>
        </b-col>
      </b-row>
    </b-container>
    
    <b-modal ref="modal" id="modal1" title="添加设备" @ok="handleOk" @shown="clearData">
      <b-input-group size="lg" prepend="序列号" class="mb-4">
        <b-form-input
          :value="serialNumber"
          @input="updateSerialNumber"
          ></b-form-input>
      </b-input-group>
      <b-input-group size="lg" prepend="密码" class="mb-4">
        <b-form-input
          :value="password"
          @input="updatePassword"
          ></b-form-input>
      </b-input-group>
      <b-form-select
        :options="options"
        :value="type"
        @change="updateType"
        />
    </b-modal>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex'
export default {
  name: 'Admin',
  data() {
    return {
      fields: ['id', 'serialNumber', 'password', 'type'],
      selected: null,
      options: [
        { value: 'C1', text: 'C1' },
        { value: 'T3', text: 'T3' },
        { value: 'T2', text: 'T2' },
        { value: 'T1', text: 'T1' },
        { value: 'F1', text: 'F1' }
      ]
    }
  },
  created () {
    this.getBackupDevices()
  },
  computed: {
    ...mapState(('admin'), [
      'items',
      'serialNumber',
      'password',
      'type',
      'error'
    ])
  },
  methods: {
    handleOk (evt) {
      evt.preventDefault()
      if (!this.serialNumber) {
        alert('请输入序列号')
      } else if (!this.password) {
        alert('请输入密码')
      } else if (!this.type) {
        alert('请选择类型')
      } else {
        this.addDevice()
        this.$refs.modal.hide()
      }
    },
    ...mapActions(('admin'), [
      'getBackupDevices',
      'addDevice'
    ]),
    ...mapMutations(('admin'), [
      'updateSerialNumber',
      'updatePassword',
      'updateType',
      'clearData'
    ])
  }
}
</script>

